import React from 'react'

const Teamcard = (props) => {
    return (
        <>
        <div className="card">
          <a href={props.link} target="_blank" rel="noopener noreferrer">
        <div className="box">
          <img src={props.name}  alt="" />
          <div className="text">{props.title}</div>
        </div>
        </a>
      </div>  
        </>
    )
}
export default Teamcard;