import React from 'react';
// import Slickslider from './Slickslider';
import Teamcard from './Teamcard';
const Team = (props) => {
    return (
        <>
            <section className="sec team" id="team">
                <h2 className="title">{props.title}</h2>
                <div className="max-width">
                    <div className="sec-content team-content">
                        {/* <Slickslider /> */}
                        <Teamcard link="http://www.gurugramflats.com/" title="Real Estate website" name="./images/gurugramflats.PNG" />         
                        <Teamcard link="http://foodorder.42web.io/" title="Food ordering website" name="./images/foodorder.PNG" />
                        <Teamcard link="http://webex2.epizy.com/" title="German Legal licence website" name="./images/german.PNG" />
                        <Teamcard link="https://ashishrana0.github.io/weatherinfo/" title="Weather website" name="./images/weather.PNG" />
                        <Teamcard link="https://ashishrana0.github.io/textutility/" title="Text-utility" name="./images/text.PNG"/>
                        <Teamcard link="https://ashishrana0.github.io/w-com/" title="Flipkart Clone" name="./images/flipkart.PNG" />
                    </div>
                </div>
            </section>
        </>
    )
}
export default Team